import { Block, BlockChildren, Data } from '@ali/4ever-cangjie';

interface CodeData extends Data {
  readonly id?: string;
  readonly syntax?: string;
  readonly theme?: string;
  readonly height?: number;
}

type CodeChildren = BlockChildren;

export interface CodeProperties<D = CodeData, N extends BlockChildren = CodeChildren> {
  data?: D;
  nodes?: N;
}

export default class Code extends Block<CodeData, CodeChildren> {
  static TYPE = 'code';

  static create<SD = CodeData, SN extends BlockChildren = CodeChildren>(
    properties: Code | CodeProperties<SD, SN> | string,
  ) {
    if (typeof properties === 'string') {
      throw new Error('`Code.create` does not accept string');
    }
    if (Code.isCode(properties)) {
      return properties as Block<SD, SN>;
    }
    return super.create<SD, SN>({
      ...properties,
      type: Code.TYPE,
    });
  }

  static isCode(maybeCode: any): maybeCode is Code {
    return Block.isBlock(maybeCode) && maybeCode.type === Code.TYPE;
  }
}
